<template key="fashion">
  <div class="page page_fashion" >
    <app-banner
      :image="page_fashion[$route.params.lang]['banner']['image']"></app-banner>
    <app-gallery
      :items="page_fashion[$route.params.lang]['gallery']"></app-gallery>
  </div>
</template>

<script>
  import gallery from './idar/gallery.vue';
  import banner from './idar/banner.vue';
  export default {
    data() {
      return {
        page_fashion: {
          ru: {
            banner: {
              image: "/static/img/Fashion_intro.png",
            },
            gallery: {
              category_1: {
                image: "/static/img/Fashion_spring.png",
                text: "Весна"
              },
              category_2: {
                image: "/static/img/Fashion_summer.png",
                text: "Лето"
              },
              category_3: {
                image: "/static/img/Fashion_autumn.png",
                text: "Осень"
              },
              category_4: {
                image: '/static/img/Fashion_winter.png',
                text: "Зима"
              },
              videos: {
                image: '/static/img/Fashion_video.png',
                text: "Видео"
              },

            }
          },
          en: {
            banner: {
              image: "/static/images/fashion.jpg",
            },
            gallery: {
              category_1: {
                image: "/static/img/Fashion_spring.png",
                text: "spring"
              },
              category_2: {
                image: "/static/img/Fashion_summer.png",
                text: "summer"
              },
              category_3: {
                image: "/static/img/Fashion_autumn.png",
                text: "autumn"
              },
              category_4: {
                image: '/static/img/Fashion_winter.png',
                text: "winter"
              },
              videos: {
                image: '/static/img/Fashion_video.png',
                text: "Videos"
              },




            }
          }
        }
      }
    },
    components: {
      appGallery: gallery,
      appBanner: banner,
    }
  }
</script>


